                  </div>
                  <div class="form-line">
                      <!-- <label for="password">验证码</label> -->
                      <input v-model="verify" class="input-data shadow" type="text" name="yzm" placeholder="请输入您的验证码">
                  </div>
                  
                 
                  <router-link to="/mine/login">返回登录？</router-link>
