<script>

import { messageBox } from '@/components/JS';
import { setInterval } from 'timers';
export default {
    name : 'register',
    data(){
        return {
            email : '',
            username : '',
            password : '',
            verify : '',
            verifyInfo : '发送验证码',
            disabled : false
        }
    },
    methods : {
        handleToVerify(){
            
            if(this.disabled){ return; }

            this.axios.get('/api/users/verify?email=' + this.email ).then((res)=>{
                var status = res.data.status;
                var This = this;
                if( status === 0 ){
                    messageBox({
                        title : '验证码',
                        content : '验证码已发送',
                        ok : '确定',
                        handleOk(){
                            This.countDown();
                        }
                    });
                }
                else{
                     messageBox({
                        title : '验证码',
                        content : '验证码发送失败',
                        ok : '确定'
                    });
                }
            });
        },
        handleToRegister(){
          // console.log(this.verify)
            this.axios.post('/api/users/register',{
                email : this.email,
                username : this.username,
                password : this.password,
                verify : this.verify
            }).then((res)=>{
                var status = res.data.status;
                var This = this;
                if(status === 0){
                    messageBox({
                        title : '注册',
                        content : '用户注册成功',
                        ok : '确定',
                        handleOk(){
                            This.$router.push('/mine/login');
                        }
                    });
                }
                else{
                     messageBox({
                        title : '注册',
                        content : res.data.msg + ', 请重新注册',
                        ok : '确定'
                    });
                }
            });
        },
        countDown(){
            this.disabled = true;
            var count = 60;
            var timer = setInterval(()=>{
                count--;
                this.verifyInfo = '剩余' + count +'秒';
                if( count === 0 ){
                    this.disabled = false;
                    count = 60;
                    this.verifyInfo = '发送验证码';
                    clearInterval(timer._id);
                }
            },1000);
        }
    }
}
</script>

